






































import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		...mapMutations(['logout']),

		navTo(url){
			this.$api.msg(`跳转到${url}`);
		},
		//退出登录
		toLogout(){
			uni.showModal({
				title: '确定要退出登录么？',
				success: (e)=>{
					if(e.confirm){
						this.logout();
						setTimeout(()=>{
							uni.navigateBack();
						}, 200)
					}
				}
			})
		},
		//switch
		switchChange(e){
			console.log(e.detail)
			let statusTip = e.detai ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
